﻿import { AnalyticsUtil, ModuleC, ModuleManager } from "odin";
import { C2CEvent, consts, ProLoadGuid } from "../../consts/ProLoadGuid";
import { MsgReport } from "../../utils/MsgReporter";
import { SoundConfigID, SoundPlay } from "../../utils/SoundPlay";
import LevelModuleC from "../level/module/LevelModuleC";
import { SkillModule_C } from "../skill/SkillModule_C";
import { PlayerDataHelper } from "./PlayerDataHelper";
import { PlayerModuleS } from "./PlayerModudleS";

export class PlayerModuleC extends ModuleC<PlayerModuleS, PlayerDataHelper> {

	onAwake(): void {

	}
	private deadTrigger: Gameplay.Trigger;
	private isDead = false;

	onStart(): void {
		this.initPlayerTrigger();
	}
	private movePos: Type.Vector = new Type.Vector(0, 0, 1);
	private nowPos: Type.Vector = new Type.Vector(0, 0, 0);
	private isEnding: boolean = false;
	onUpdate(dt: number): void {
		if (this.deadTrigger) {
			this.deadTrigger.setRelativeLocation(this.nowPos.add(this.movePos));
			this.movePos.z = -this.movePos.z;
		}
	}
	/**玩家位置移动
	 * @param pos 目标点位置
	 */
	net_PlayerMovePosition(pos: Type.Vector) {
		this.server.net_PlayerMovePosition(pos, false);
	}
	/**初始化玩家身上的触发器 */
	initPlayerTrigger() {
		let player = Gameplay.getCurrentPlayer();
		player.character.cameraSystem.cameraCollisionEnable = false;
		player.character.ready().then(() => {
			Core.GameObject.asyncSpawnGameObject(ProLoadGuid.Trigger).then((obj: Gameplay.Trigger) => {
				obj.attachToGameObject(player.character);
				this.deadTrigger = obj;
				obj.setWorldScale(new Type.Vector(0.5, 0.5, 1));
				obj.setRelativeLocation(new Type.Vector(0, 0, 0));
				this.nowPos = new Type.Vector(0, 0, 0);
				obj.onEnter.add((other) => {
					if (other && other.tag && !this.isDead) {
						let gameobj = other as Core.GameObject;
						let parentObj = gameobj.parent;
						while (parentObj.parent) {
							if (parentObj.parent) {
								parentObj = parentObj.parent;
							}
						}
						let scrpit = parentObj.getScripts()[0];
						let trapId = 0;
						if (scrpit) {
							trapId = scrpit["type"];
						}
						this.judgeTag(other.tag, trapId);
					}
				});
			});
		});
	}
	judgeTag(tag: string, trapId: number) {
		if (tag.includes(consts.deadTag)) {
			if (!this.isDead) {
				SoundPlay.ins.play(SoundConfigID.PLAYER_AND_BARRIER);
				this.deadToReStart();
			}
			// 结算时失败发送埋点 to do
			let msg = AnalyticsUtil.get(MsgReport.ts_action_dead)
			msg.data.death_type = trapId;
			msg.data.stage_level = ModuleManager.instance.getModule(LevelModuleC).getCurrentSceneLv();
			msg.send();
		}
		for (let i = 0; i < consts.testSkillTypeName.length; i++) {
			let skilltag = consts.testSkillTypeName[i];
			if (tag.includes(skilltag)) {
				Events.dispatchLocal(C2CEvent.SKILL_DOORGET, tag);
				ModuleManager.instance.getModule(SkillModule_C).net_SetGatherSkills(i);
				// 玩家每触发技能门都发送一次
				ModuleManager.instance.getModule(LevelModuleC).setMsgStep(2);

				// 玩家选择的技能 埋点
				let curDoor = this.data.getSkillDoor()[i];
				if (curDoor == false) {
					let msgDoor = AnalyticsUtil.get(MsgReport.ts_interaction)
					msgDoor.data.interaction_id = i + 1;
					msgDoor.send();
					curDoor = true;
					this.server.net_setSkilldoor(i);
				}

				break;
			}
		}
		// if(tag.includes())
	}
	/**玩家死亡重新开始 */
	deadToReStart() {
		SoundPlay.ins.play(SoundConfigID.DIE);
		let player = Gameplay.getCurrentPlayer();
		this.isDead = true;
		player.character.switchToWalking();
		player.character.playAnimationLocally(ProLoadGuid.playerDead);
		player.character.moveEnable = false;
		if (this.deadTrigger) {
			this.deadTrigger.setCollisionEnabled(false);
		}
		Events.dispatchLocal(C2CEvent.HALLUI_UIOUT, false);
		//todo：显示结算、通知关卡玩家死亡
		let moduleLevel = ModuleManager.instance.getModule(LevelModuleC)
		// to do 
		ModuleManager.instance.getModule(LevelModuleC).setMsgStep(3);
		moduleLevel.setDefaultTime();
	}
	/**心灵宝石，玩家飞行 */
	heartSetPlayerFly() {
		let player = Gameplay.getCurrentPlayer();
		player.character.switchToFlying();
		if (this.deadTrigger) {
			this.deadTrigger.setWorldScale(new Type.Vector(0.5, 0.5, 0.5));
			this.deadTrigger.setRelativeLocation(new Type.Vector(0, 0, 40));
			this.nowPos = new Type.Vector(0, 0, 40);
		}
		let pos = player.character.getWorldLocation();
		player.character.worldLocation = pos.add(new Type.Vector(0, 0, 100));
		// this.server.net_PlayerMovePosition(pos.add(new Type.Vector(0, 0, 100)), false);
	}
	/**重新开始游戏- 心灵宝石取消 */
	mindSkillReset() {
		let player = Gameplay.getCurrentPlayer();
		player.character.playAnimationLocally(ProLoadGuid.playerStand);
		player.character.moveEnable = true;
		let pos = player.character.getWorldLocation();
		// this.server.net_PlayerMovePosition(pos.add(new Type.Vector(0, 0, 100)), true);
		player.character.worldLocation = pos.add(new Type.Vector(0, 0, 100));
		setTimeout(() => {
			this.isDead = false;
			this.isEnding = false;
		}, 1000)
		player.character.switchToWalking();
		if (this.deadTrigger) {
			this.deadTrigger.setWorldScale(new Type.Vector(0.5, 0.5, 1));
			this.deadTrigger.setRelativeLocation(new Type.Vector(0, 0, 0));
			this.nowPos = new Type.Vector(0, 0, 0);
			this.deadTrigger.setCollisionEnabled(true);
		}
	}
	/**终局 */
	endGame() {
		this.isEnding = true;
	}
	getIsEnd() {
		return this.isEnding;
	}
	net_moveEnd() {
		this.isDead = false;
	}
	resrtSkilldoor() {
		this.server.net_resrtSkilldoor();
	}
}
