﻿import { ModuleS } from "odin";
import { PlayerDataHelper } from "./PlayerDataHelper";
import { PlayerModuleC } from "./PlayerModuleC";

export class PlayerModuleS extends ModuleS<PlayerModuleC, PlayerDataHelper> {

	onStart(): void {

	}
	onPlayerEnterGame(player: Gameplay.Player, reenter: boolean): void {

	}
	/**
	 * 玩家位置移动
	 * @param pos 移动的目标点位置
	 */
	net_PlayerMovePosition(pos: Type.Vector, isGoStart = false, player?: Gameplay.Player) {
		player.character.setWorldLocation(pos);// = pos;
		// this.client.net_moveEnd();
		if (isGoStart) {
			this.getClient(player).net_moveEnd()
		}
	}
	net_setSkilldoor(i: number, player?: Gameplay.Player) {
		let data = this.getPlayerData(player);
		data.getSkillDoor()[i] = true;
	}
	net_resrtSkilldoor(player?: Gameplay.Player) {
		let data = this.getPlayerData(player);
		let skillDoor = data.getSkillDoor();
		skillDoor = [false, false, false, false, false, false];
	}
}
