﻿export interface IBarrier {

	/**恢复 */
	resetPrefab: Function;
	/**毁灭 */
	destroyPrefab: Function;
	/**空间 */
	space?: Function;
	/**时间 */
	time?: Function;
	/**力量 */
	power?: Function;
	/**现实 */
	reality?: Function;
	/**冥想 */
	mind?: Function;
	/**技能门类型列表 --传数组*/
	setSkillType?: (types: Array<string>) => void
}
export default class WallDoorTrapSkill extends Core.Script implements IBarrier {
	@Core.Property({ displayName: "需要放大的倍数" })
	worldScale: number = 10;
	private skillTag = "";
	private trigger: Core.GameObject;
	private deadInterval = null;
	private moveTime = 1000;
	private moveLength = 300;
	onStart() {
		// this.setWall(true)
		this.showSkill(true);
		Events.addLocalListener("skill_doorget", (inTag) => {
			if (this.skillTag == inTag) {
				// this.trigger.setCollisionEnabled(false);
				this.trigger.setCollision(Type.CollisionStatus.Off);
				this.playGetAnimation();
			}
		});

	}
	playGetAnimation() {
		if (this.deadInterval) clearInterval(this.deadInterval);
		this.showSkill(false);
	}
	resetPos(obj: Core.GameObject) {
		let rePos = obj.getRelativeLocation();
		obj.setRelativeLocation(new Type.Vector(rePos.x, rePos.y, 0));
	}
	showSkill(show: boolean) {
		if (this.deadInterval) clearInterval(this.deadInterval);
		this.setWall(show);
		let ball = this.gameObject.getChildByName("skillBall");
		let left = this.gameObject.getChildByName("base_1");
		let right = this.gameObject.getChildByName("base_2");
		let ui = this.gameObject.getChildByName("skillTypeUI");
		let wall1 = this.gameObject.getChildByName("wall_1");
		let wall2 = this.gameObject.getChildByName("wall_2");
		if (show) {
			ui.setVisibility(Type.PropertyStatus.On);
			ball.setVisibility(Type.PropertyStatus.On);
			this.resetPos(left);
			this.resetPos(right);
			this.resetPos(wall1);
			this.resetPos(wall2);
		} else {
			ui.setVisibility(Type.PropertyStatus.Off);
			ball.setVisibility(Type.PropertyStatus.Off);
			let leftpos = left.worldLocation;
			let rightpos = right.worldLocation;
			let wall1pos = wall1.worldLocation;
			let wall2pos = wall2.worldLocation;
			let time = 0;
			let num = this.moveTime / 10;
			let single = this.moveLength / num;
			this.deadInterval = setInterval(() => {
				leftpos.z -= single;
				rightpos.z -= single;
				wall1pos.z -= single;
				wall2pos.z -= single;

				left.worldLocation = leftpos;
				right.worldLocation = rightpos;
				wall1.worldLocation = wall1pos;
				wall2.worldLocation = wall2pos;
				time++;
				if (time >= num) {
					clearInterval(this.deadInterval);
				}
			}, 10);
		}
	}
	setWall(show: boolean) {
		let wall1 = this.gameObject.getChildByName("wall_1");
		let wall2 = this.gameObject.getChildByName("wall_2");
		if (wall1) {
			show ? wall1.tag = "dead" : wall1.tag = "";
			show ? wall1.setCollision(Type.PropertyStatus.On) : wall1.setCollision(Type.PropertyStatus.Off);
		}
		if (wall2) {
			show ? wall2.tag = "dead" : wall2.tag = "";
			show ? wall2.setCollision(Type.PropertyStatus.On) : wall2.setCollision(Type.PropertyStatus.Off);
		}
	}
	setSkillType(types: Array<string>) {
		this.setSkillName(this.gameObject, types[0])
	}
	setSkillName(obj: Core.GameObject, type: string) {
		let info = type.split("_");
		let txtColor = info[0];
		let typeName = info[1];
		let ballGuid = info[2];
		let showName = info[3];
		//////////////
		let ui = obj.getChildByName("skillTypeUI") as Gameplay.UIWidget;
		let tag = ui.getUI();
		let txt = tag.rootContent.getChildByName("mText_SkillName") as UI.TextBlock;
		if (txt) {
			txt.text = (showName);
			txt.setFontColorByHex(txtColor);
		}
		ui.refresh();

		let ball = obj.getChildByName("skillBall") as Gameplay.StaticMesh;
		ball.setMaterial(ballGuid);

		let trigger = obj.getChildByName("boxTrigger");
		if (!trigger) return;
		this.trigger = trigger;
		let tagStr = typeName + "_" + obj.guid;
		trigger.tag = tagStr;
		this.skillTag = tagStr
		this.trigger.setCollision(Type.CollisionStatus.QueryOnly);
	}
	resetPrefab() {
		this.gameObject.setVisibility(Type.PropertyStatus.On);
		this.trigger.setCollision(Type.CollisionStatus.QueryOnly);
		this.showSkill(true);
	};
	destroyPrefab() {
		// this.gameObject.visibility=(Type.PropertyStatus.Off);
	};
	space() {
		// 
	};
	time?: Function;
	power?: Function;
	reality?: Function;
	mind?: Function;

}
