﻿import { DataInfo, ModuleData } from "odin";
import { consts } from "../../consts/ProLoadGuid";
export enum SkillType {
	Power,
	Space,
	Reality,
	Time,
	Heart,
	Soul
}
export class SkillData extends DataInfo {
	/**技能等级列表 力量-空间-现实-时间-心灵 */
	skillLvList: Array<number> = [];
	/**各技能当前技能点列表 */
	skillUsePointsList: Array<number> = [];
	/**技能未使用技能点列表 */
	skillHavePointsList: Array<number> = [];
	/**是否拥有灵魂宝石 */
	haveSoul: boolean = false;

	/**已获取过的技能宝石列表 */
	skillGetList: Array<boolean> = [];
}
export class SkillDataHelper extends ModuleData<SkillData> {

	constructor() {
		super(SkillData);
	}
	protected initDefaultData(): void {
		if (!this.dataInfo.skillLvList || this.dataInfo.skillLvList.length <= 0) {
			this.dataInfo.skillLvList = [1, 1, 1, 1, 1];
		}
		if (!this.dataInfo.skillUsePointsList || this.dataInfo.skillUsePointsList.length <= 0) {
			this.dataInfo.skillUsePointsList = [0, 0, 0, 0, 0];
		}
		if (!this.dataInfo.skillHavePointsList || this.dataInfo.skillHavePointsList.length <= 0) {
			this.dataInfo.skillHavePointsList = [0, 0, 0, 0, 0];
		}
		if (!this.dataInfo.skillGetList || this.dataInfo.skillGetList.length <= 0) {
			this.dataInfo.skillGetList = [false, false, false, false, false];
		}
		this.saveData(false);
	}
	protected onDataInit(): void {
		if (!this.dataInfo.skillGetList || this.dataInfo.skillGetList.length <= 0) {
			this.dataInfo.skillGetList = [false, false, false, false, false];
		}
		this.saveData(false);
	}
	/**
	 * 获得某技能的技能点
	 * @param type 技能类型
	 * @param num 获得的技术点数量
	 */
	skillHavePointGet(type: SkillType, num: number) {
		this.dataInfo.skillHavePointsList[type] += num;
		this.saveData(false);
	}
	/**
	 * 技能花费技能点
	 * @param type 技能类型
	 */
	skillUsePoint(type: SkillType) {
		this.dataInfo.skillHavePointsList[type] -= 1;
		this.dataInfo.skillUsePointsList[type] += 1;
		if (this.dataInfo.skillUsePointsList[type] >= 5) {
			this.dataInfo.skillUsePointsList[type] = 5;
		}
		this.saveData(false);
	}
	/**
	 * 技能升级
	 * @param type 技能类型 
	 */
	skillLvUp(type: SkillType) {
		this.dataInfo.skillLvList[type] += 1;
		this.dataInfo.skillUsePointsList[type] = 0;
		if (this.dataInfo.skillLvList[type] > consts.skillMaxLv) {
			this.dataInfo.skillLvList[type] = consts.skillMaxLv;
		}
		this.saveData(false);
	}
	/**
	 * 获取某技能的当前使用技能点
	 * @param type 技能类型
	 */
	getSkillUsePointByType(type: SkillType): number {
		return this.dataInfo.skillUsePointsList[type];
	}
	/**
	 * 获取某技能的当前等级
	 * @param type 技能类型
	 */
	getSkillLvByType(type: SkillType) {
		return this.dataInfo.skillLvList[type];
	}
	/**
	 * 获取某类型的技能点数量
	 * @param type 技能点类型
	 * @returns 数量
	 */
	getSkillHaveByType(type: SkillType) {
		return this.dataInfo.skillHavePointsList[type];
	}
	/**返回玩家等级列表 */
	getSkillLvList() {
		return this.dataInfo.skillLvList;
	}
	/**返回技能拥有的技能点 */
	getSkillHavePoint() {
		return this.dataInfo.skillHavePointsList;
	}
	/**返回技能使用的技能点 */
	getSkillUsePoint() {
		return this.dataInfo.skillUsePointsList;
	}
	/**设置技能宝石获取表 */
	setSkillGem(type: SkillType) {
		this.dataInfo.skillGetList[type] = true;
		this.saveData(false);
	}
	/**获得技能宝石表 */
	getSkillGem() {
		return this.dataInfo.skillGetList;
	}
}
