﻿import { AnalyticsUtil, DataCenterC, ModuleManager, oTrace } from "odin";
import { C2CEvent, ProLoadGuid } from "../../consts/ProLoadGuid";
import { MsgReport } from "../../utils/MsgReporter";
import { SoundConfigID, SoundPlay } from "../../utils/SoundPlay";
import { PlayerDataHelper } from "../player/PlayerDataHelper";
import { PlayerModuleC } from "../player/PlayerModuleC";
import LevelModuleC from "./module/LevelModuleC";

@Core.Class
export default class EndTrigger extends Core.Script {

	/** 当脚本被实例后，会在第一帧更新前调用此函数 */
	protected onStart(): void {
		const trigger = this.gameObject as Gameplay.Trigger;

		if (Util.SystemUtil.isClient()) {
			trigger.onEnter.add(async (go: Core.GameObject) => {
				if ((go) instanceof Gameplay.Character) {
					let isEnd = ModuleManager.instance.getModule(PlayerModuleC).getIsEnd();
					if (isEnd) return;
					oTrace('PASS GAME');
					let ModuleC = ModuleManager.instance.getModule(LevelModuleC)
					let curLevel = ModuleC.getCurrentSceneLv();
					ModuleManager.instance.getModule(PlayerModuleC).endGame();
					const char = go as Gameplay.Character;
					char.switchToWalking();
					let successDanceGuid = ProLoadGuid.playerSuccess.split(",");
					char.playAnimation(successDanceGuid[Math.floor(Math.random() * (successDanceGuid.length - 0.01))], 0, 1)

					const cameraMoveAni = new Util.TweenUtil.Tween({ trans: char.cameraSystem.cameraRelativeTransform, leng: char.cameraSystem.targetArmLength }).to({ trans: new Type.Transform(new Type.Vector(600, 0, 0), new Type.Rotation(0, 0, 180), Type.Vector.one), leng: 200 }, 500).onUpdate(obj => {
						char.cameraSystem.cameraRelativeTransform = obj.trans
						char.cameraSystem.targetArmLength = obj.leng
					}).start();

					let ModuleData = DataCenterC.instance.getData(PlayerDataHelper);
					let skillData = ModuleData.getSkillDoor();
					skillData = [false, false, false, false, false, false];
					ModuleManager.instance.getModule(PlayerModuleC).resrtSkilldoor();

					// 胜利埋点
					let msg = AnalyticsUtil.get(MsgReport.ts_game_over);
					msg.data.round_id = curLevel;
					msg.data.fail_num = ModuleC.getDefaultTime();
					ModuleC.resetDefaultTime();
					msg.send();

					// 核心循环结束埋点
					ModuleManager.instance.getModule(LevelModuleC).setLevelNum();



					// 新手引导相关埋点
					// to do 
					switch (curLevel) {
						case 1:
							this.sendMsg("step1");
							break;
						case 2:

							this.sendMsg("step2");
							break;
						case 3:
							this.sendMsg("step3");
							break;
						case 4:
							this.sendMsg("step4");
							break;
						case 5:
							this.sendMsg("step5");
							break;
						case 6:
							let msg6 = AnalyticsUtil.get(MsgReport.ts_tutorial_end);
							msg6.send();
							break;
					}

					// UI操作 
					// ModuleC.showEndUI(true);
					// 核心循环埋点
					ModuleManager.instance.getModule(LevelModuleC).setMsgStep(4);


					// ModuleManager.instance.getModule(LevelModuleC).showEndUI(true);
					Events.dispatchLocal(C2CEvent.HALLUI_UIOUT, true);
					// UI.instance.showPanel()
					// ModuleManager.instance.getModule(LevelModuleC).passGame();
					SoundPlay.ins.play(SoundConfigID.PASS_GAME_TALK);
				}
			});
		}
	}
	private sendMsg(stepName: string) {
		let msg = AnalyticsUtil.get(MsgReport.ts_tutorial_step);
		msg.data.tutorial_step = stepName;
		msg.send();
	}

}
