import { ModuleS } from "odin";
import { BarrierInfo, GameCoefficient, LevelDataHelper } from "./LevelDataHelper";
import LevelModuleC from "./LevelModuleC";

const ROAD_GUID = 'B0FF298DFCBAC149';// 跑道guid
export default class LevelModuleS extends ModuleS<LevelModuleC, LevelDataHelper>{
    /**
     * 保存关卡障碍物数据
     * @param level 关卡等级
     * @param barrierInfo 关卡障碍物信息 
     * @param pos 出生的位置
     * @param gameCoefficient 游戏系数
     */
    net_saveLevelData(level: number, barrierInfo: BarrierInfo[], pos: number[], gameCoefficient: GameCoefficient, player?: Gameplay.Player) {
        const p = player!;
        const pData = this.getPlayerData(p);
        pData.saveLevelInfo(level, barrierInfo, pos, gameCoefficient);
        // 因为是所有障碍物都会改变，所以直接双端修改
        pData.saveData(true);
    }

    /**
     * 保存关卡lv
     * @param level 当前关卡
     * @param player 
     */
    net_saveLevel(level: number, player?: Gameplay.Player) {
        const p = player!;
        const pData = this.getPlayerData(p);
        // oTrace('ssssssss', level);

        if (pData.getLevel() < level) {
            pData.saveLevel(level);
            pData.saveData(false);
            return true;
        }

        return false;
    }

    /**
     * 拉伸地面
     * @param scaleX x的缩放 
     * @param isTween 是否有动画
     * @param player 
     */
    net_scaleRoad(scaleX: number, isTween: boolean, player?: Gameplay.Player) {
        const obj = Core.GameObject.find(ROAD_GUID);
        const toVec = new Type.Vector(scaleX, 1, 1);
        if (isTween) {
            new Util.TweenUtil.Tween({ worldScale: obj.getWorldScale() })
                .to({ worldScale: toVec }, 1000)
                .onUpdate((o) => {
                    obj.setWorldScale(o.worldScale);
                })
                .start();
        } else {
            obj.setWorldScale(toVec);// = toVec;
        }
    }

    net_resetDefaultTime(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.resetDefaultTime();
    }
    net_setDefaultTime(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.setDefaultTime();
    }

    net_setNewPlayer(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.setNewPlayer();
    }
    net_setIfNewEnter(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.setIfNewEnter();
    }
    net_setLevelDay(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.getDate().levelDay++;
    }

    net_addSkilNum(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.getDate().skillSum++;
    }
    net_addLevelNum(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.checkIsSendMsg(player);
    }
    /**
     * 修改完成的相应埋点步骤
     * @param num 完成的第几个
     * @param player 玩家
     */
    net_setMsgStep(num: number, player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        data.getDate().sendMsgStep[num - 1] = true;
    }
    /**
     * 还原核心循环埋点的数据
     * @param player 玩家
     */
    net_resetMsgStep(player?: Gameplay.Player) {
        let data = this.getPlayerData(player);
        let sendMsgStep = data.getDate().sendMsgStep;
        for (let i = 0; i < sendMsgStep.length; i++) {
            sendMsgStep[i] = false;
        }
    }
    /**
     * 玩家完成核心循环步骤加1
     * @param player 玩家
     */
    net_setColNum(player?: Gameplay.Player) {
        let data = this.getPlayerData(player)
        data.getDate().sendStepTimes++;
    }
}