import { AnalyticsUtil, DataInfo, ModuleData } from "odin";
import { MsgReport } from "../../../utils/MsgReporter";
/**障碍物信息 */
export class BarrierInfo {
    prefabID = 0;
    prefabLoc: number[] = [];
}

/**游戏系数 */
export class GameCoefficient {
    speedCoefficient = 0;// 速度系数
    constructor(speed: number) {
        this.speedCoefficient = speed;
    }
}

export class LevelData extends DataInfo {
    level = 0;// 正确的关卡lv
    oldLevel = 0;// 机关绑定的关卡lv
    startPos = [];// 出发位置
    gameCoefficient: GameCoefficient = null;// 游戏参数系数
    barrierInfo: BarrierInfo[] = [];

    ifNewPlayer: boolean = true;

    fallTime: number = 0;
    ifNewEnter: boolean = true;

    // 当天累计闯过的关卡
    levelDay: number = 0;

    public sendMSGTime: number = 0;
    // 玩家在整个游戏中消耗的技能点的总和
    public skillSum: number = 0;
    // 核心循环记录全为true的时候发送一次
    public sendMsgStep: boolean[] = [false, false, false, false];
    // 玩家完成核心循环的次数
    public sendStepTimes: number = 0;
}

export class LevelDataHelper extends ModuleData<LevelData> {

    constructor() {
        super(LevelData);
    }

    /**
     * 保存关卡障碍物数据
     * @param level 障碍物对应的关卡lv
     * @param barrierInfo 障碍物信息
     * @param pos 出生位置
     * @param gameCoefficient 当前关卡的参数系数 
     */
    saveLevelInfo(level: number, barrierInfo: BarrierInfo[], pos: number[], gameCoefficient: GameCoefficient) {
        this.dataInfo.oldLevel = level;
        this.dataInfo.barrierInfo = barrierInfo;
        this.dataInfo.startPos = pos;
        this.dataInfo.gameCoefficient = gameCoefficient;
    }

    /**
     * 保存关卡等级
     * @param level 
     */
    saveLevel(level: number) {
        this.dataInfo.level = level;
    }

    /**
     * 获得起始位置
     */
    getStartPos() {
        return this.dataInfo.startPos;
    }

    /**
     * 当前关卡
     */
    getLevel() {
        return this.dataInfo.level;
    }

    /**
     * 障碍物关卡
     */
    getBarrierLevel() {
        return this.dataInfo.oldLevel;
    }

    /**
     * 关卡障碍物信息
     */
    getBarrierInfo() {
        return this.dataInfo.barrierInfo;
    }

    /**获取游戏系数 */
    getGameCoefficient() {
        return this.dataInfo.gameCoefficient;
    }

    resetDefaultTime() {
        this.dataInfo.fallTime = 0;
    }
    setDefaultTime() {
        this.dataInfo.fallTime++;
    }
    getDefaultTime() {
        return this.dataInfo.fallTime;
    }

    getIfNewPlayer() {
        return this.dataInfo.ifNewPlayer;
    }

    setNewPlayer() {
        this.dataInfo.ifNewPlayer = false;
    }
    getIfNewEnter() {
        return this.dataInfo.ifNewEnter;
    }

    setIfNewEnter() {
        this.dataInfo.ifNewEnter = false;
    }
    getLevelDay(): number {
        return this.dataInfo.levelDay;
    }

    checkIsSendMsg(player: Gameplay.Player) {
        let starTime = new Date().getTime();
        let curTime = Math.floor(this.dataInfo.sendMSGTime / 86400000);
        let perTime = Math.floor(starTime / 86400000);
        // 第一个条件判断是否同一天
        if (curTime < perTime) {
            // 是同一天再考虑关卡数
            this.dataInfo.levelDay += 1;
            if (this.dataInfo.levelDay < 2) return;
            this.dataInfo.sendMSGTime = starTime;
            let msg = AnalyticsUtil.get(MsgReport.ts_coreGameplay_end);
            msg.send(player);
            this.dataInfo.levelDay = 0;
            this.saveData(false);


        }
    }
    getDate(): LevelData {
        return this.dataInfo;
    }
}
