﻿import { ModuleC, ModuleManager } from "odin";
import { GameConfig } from "../../config/GameConfig";
import { C2CEvent, consts, ProLoadGuid, ProLoadGuid_SceneGuid } from "../../consts/ProLoadGuid";
import SkillPanelUI from "../../ui/SkillPanelUI";
import SoulUI from "../../ui/SoulUI";
import { SoundConfigID, SoundPlay } from "../../utils/SoundPlay";
import Tools from "../../utils/Tools";
import LevelModuleC from "../level/module/LevelModuleC";
import { PlayerModuleC } from "../player/PlayerModuleC";
import { SkillDataHelper, SkillType } from "./SkillDataHelper";
import { SkillModule_S } from "./SkillModule_S";

export class SkillModule_C extends ModuleC<SkillModule_S, SkillDataHelper>{
    private skillPanel: SkillPanelUI;
    onStart(): void {
        this.skillPanel = UI.UIManager.instance.getUI(SkillPanelUI);// UI.instance.getPanel(SkillPanelUI);
    }
    /**
     * 技能宝石设置，用来确认玩家时候领取过某宝石
     * @param type 
     */
    net_SetGatherSkills(type: SkillType) {
        let list = this.data.getSkillGem();
        if (list[type] == false) {
            this.data.setSkillGem(type);
            this.server.net_SetGatherSkills(type);
        }
        this.net_GetSkill(type);
    }
    /**获取技能宝石 */
    net_GetGatherSkills() {
        return this.data.getSkillGem();
    }
    ///////////////技能点-技能加点面板逻辑相关
    /**
     *技能点数量增长
     * @param type 技能类型
     * @param num 获得的技能点数量
     */
    net_SkillPointGet(type: SkillType, num: number) {
        this.data.skillHavePointGet(type, num);
        this.server.net_SkillPointGet(type, num);
    }
    /**
     * 技能升级技能点添加
     * @param type 
     */
    net_SkillLvUpPoint(type: SkillType) {
        let lv = this.data.getSkillLvByType(type);
        if (lv >= consts.skillMaxLv) return;
        SoundPlay.ins.play(SoundConfigID.SKILL_UP);

        let havePoints = this.data.getSkillHaveByType(type);
        if (havePoints <= 0) return;
        this.data.skillUsePoint(type);
        this.server.net_SkillLvUpPoint(type);
        let point = this.data.getSkillUsePointByType(type);
        this.skillPanel.skillAddPoint(type, this.data.getSkillHaveByType(type), point,
            lv);
        if (this.juageSkillLvPoints(lv, point)) {
            this.skillLvUp(type);
        }
    }
    /**
     * 玩家技能等级提升
     * @param type 
     */
    skillLvUp(type: SkillType) {
        this.data.skillLvUp(type);
        this.skillPanel.skillLvUp(type, this.data.getSkillLvByType(type));
        this.server.net_SkillLvUp(type);
        Tools.playDynamic(30);
    }
    /**
     * 判断技能是否升级
     * @param lv 
     * @param point 
     */
    juageSkillLvPoints(lv: number, point: number) {
        if (lv >= consts.skillMaxLv) return;

        if (point >= lv + 1) {
            return true;
        }
        return false;
    }

    /**
     *获取当前等级距离最高等级所需技能点数量 
     * @param lv 
     * @param usePoints 
     */
    getMaxNeedPoint(lv: number, usePoints: number) {
        if (lv >= consts.skillMaxLv) return 0;
        let needPoints = 0;
        for (let i = lv; i < consts.skillMaxLv; i++) {
            needPoints += (i + 1);
        }
        return needPoints - usePoints;
    }

    /**获取技能等级列表 */
    net_GetSKillLvList() {
        return this.data.getSkillLvList();
    }
    /**获取加点数据 */
    net_GetSkillUsePoints() {
        return this.data.getSkillUsePoint()
    }
    /**获取拥有的技能点列表 */
    net_GetSkillHavePoints() {
        return this.data.getSkillHavePoint();
    }
    /////////////////技能获取-释放-主ui变动等逻辑
    /**重置/通关 重置技能获取 */
    net_SkillReset() {
        this.useMind = false;
        this.useReality = false;
        this.useSpace = false;
        this.useTime = false;

        if (this.powerBallAncher) {

            for (let i = 0; i < this.powerBallList.length; i++) {
                if (this.powerBallList[i]) {
                    this.powerBallList[i].detachFromGameObject();
                    // let trigger = this.powerBallList[i].getChildByName("Ball").getChildByName("BallTrigger") as Gameplay.Trigger;
                    // if (trigger) {
                    //     trigger.setCollisionEnabled(false);
                    // }
                    // Extension.GameObjPool.getInstance().despawn(this.powerBallList[i]);
                    this.powerBallList[i].destroy();
                }
            }
            this.powerBallList = [];
            this.powerBallAncher.destroy();
            this.powerBallAncher = null;
            // Extension.GameObjPool.getInstance().despawn(this.powerBallAncher);
        }
        this.powerBallAncher = null;
        Events.dispatchLocal(C2CEvent.HALLUI_SKILLRESET);
        ModuleManager.instance.getModule(PlayerModuleC).mindSkillReset();
    }

    private nowHaveSkill = [0, 0, 0, 0, 0, 0];
    private powerBallList: Array<Core.GameObject> = [];
    private powerBallRotaionList = [];
    private powerBallAncher: Gameplay.Anchor;
    private addRotaionSpeed: Type.Rotation = new Type.Rotation(0, 0, consts.rotationSpeed);
    /**播放技能 */
    net_GetSkill(type: SkillType) {
        let lv = this.data.getSkillLvByType(type);
        switch (type) {
            case SkillType.Power: {
                this.playSkillPower(lv);
            } break;
            case SkillType.Space: {
                this.playSkillSpace(lv);
            } break;
            case SkillType.Reality: {
                this.playSkillReality(lv);
            } break;
            case SkillType.Time: {
                this.playSkillTime(lv);
            } break;
            case SkillType.Heart: {
                this.playSkillHeart(lv);
            } break;
            case SkillType.Soul: {
                this.playSoul();
            } break;
        }
    }
    /**释放力量宝石能力
     *  @param lv 技能等级
     */
    playSkillPower(lv: number) {
        if (this.powerBallAncher) return;
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);

        const func = () => {
            SoundPlay.ins.play(SoundConfigID.PASS_POWER);
            this.playEffectAtPlayer(SkillType.Power);
            ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Power, lv);
            this.setPowerBall(lv)
        }

        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Power, func);

    }
    setPowerBall(lv) {
        let player = Gameplay.getCurrentPlayer();

        let ancher = Core.GameObject.spawnGameObject(ProLoadGuid.anchor) as Gameplay.Anchor;//Extension.GameObjPool.getInstance().spawn(ProLoadGuid.anchor) as Gameplay.Anchor;
        ancher.setWorldLocation(player.character.getWorldLocation());// = player.character.worldLocation;
        this.powerBallAncher = ancher;
        let num = GameConfig.GemSkill.getElement(1).skillValue[lv - 1];
        if (!num) num = lv + 1;
        let singleR = 360 / (num);
        let cirPosList = Tools.getCirclePoints(ancher.getWorldLocation(), consts.powerBallRadius, singleR);
        for (let i = 0; i < num; i++) {
            let ball = Core.GameObject.spawnGameObject(ProLoadGuid_SceneGuid.powerBallPre);// Extension.GameObjPool.getInstance().spawn(ProLoadGuid_SceneGuid.powerBallPre);
            let trigger = ball.getChildByName("Ball").getChildByName("BallTrigger") as Gameplay.Trigger;
            if (trigger) {
                trigger.setCollisionEnabled(true);
                trigger.onEnter.remove(this.powerTrigger);
                trigger.onEnter.add(this.powerTrigger);
            }
            this.powerBallList.push(ball);
            ball.attachToGameObject(ancher);
            let pos = cirPosList[i];
            ball.setRelativeLocation(pos);
        }
    }
    private useSpace = false;
    /**释放空间宝石 */
    playSkillSpace(lv: number) {
        if (this.useSpace) return;
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);
        const func = () => {
            SoundPlay.ins.play(SoundConfigID.PASS_SPACE);
            this.playEffectAtPlayer(SkillType.Space);
            ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Space, lv);
        }

        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        this.useSpace = true;
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Space, func);
        // let widthNum = consts.spaceBaseNum + consts.spaceLvAddNum * (lv - 1);
    }
    useReality = false;
    /**释放现实宝石 */
    playSkillReality(lv: number) {

        if (this.useReality) return;
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);
        const func = () => {
            SoundPlay.ins.play(SoundConfigID.PASS_SPACE);
            this.playEffectAtPlayer(SkillType.Space);
            ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Reality, lv);
        }

        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        this.useReality = true;
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Reality, func);
        let realityPro = consts.baseRealityProbabillity + consts.addRealityProbabillity * (lv - 1);
    }
    useTime = false;
    /**释放时间宝石 */
    playSkillTime(lv: number) {
        if (this.useTime) return;
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);
        const func = () => {
            SoundPlay.ins.play(SoundConfigID.PASS_TIME);
            this.playEffectAtPlayer(SkillType.Time);
            ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Time, lv);
        }

        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        this.useTime = true;
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Time, func);
        let timePro = consts.baseTimeProportion + consts.addTimeProportion * (lv - 1);
        //todo：通知机关延时:减速比例为timepro
    }
    useMind = false;
    /**释放心灵宝石 */
    playSkillHeart(lv: number) {
        SoundPlay.ins.play(SoundConfigID.PASS_MIND);
        if (this.useMind) return
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);
        const func = () => {
            this.playEffectAtPlayer(SkillType.Heart);
            SoundPlay.ins.stop(SoundConfigID.WALK);
            ModuleManager.instance.getModule(PlayerModuleC).heartSetPlayerFly();
            ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Heart, lv);
        }

        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        this.useMind = true;
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Heart, func);
    }
    /**释放灵魂宝石 */
    playSoul() {
        Events.dispatchLocal(C2CEvent.HALLUI_PLAYERSTOP);
        const func = () => {
            UI.UIManager.instance.show(SoulUI, () => {
                this.playEffectAtPlayer(SkillType.Soul);
                ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Soul, 1);
            })
            // UI.instance.showPanel(SoulSkill, () => {
            //     this.playEffectAtPlayer(SkillType.Soul);
            //     ModuleManager.instance.getModule(LevelModuleC).useSkillByType(SkillType.Soul, 1);
            // });
        }
        // SoundPlay.ins.play(SoundConfigID.GET_SKILL);
        Events.dispatchLocal(C2CEvent.GETSKILLGEM, SkillType.Soul, func);
    }
    powerTrigger = (obj: Core.GameObject) => {
        if (obj.tag && obj.tag.includes(consts.officeTag)) {
            // obj.visibility=(Type.PropertyStatus.Off);
            let objName = (obj as Core.GameObject).name;
            Service.EffectService.getInstance().playEffectAtLocation(
                ProLoadGuid.effect_power,
                obj.getWorldLocation(),
                1,
                undefined,
                new Type.Vector(0.3)
            );
            Events.dispatchLocal(C2CEvent.DESTORY_OFFICE, obj.tag, objName);
            SoundPlay.ins.play(SoundConfigID.BALL_AND_BARRIER);
        }
    }

    onUpdate(dt: number): void {
        if (this.powerBallAncher) {
            this.powerBallAncher.setWorldLocation(Gameplay.getCurrentPlayer().character.getWorldLocation());
            this.powerBallAncher.setWorldRotation(this.powerBallAncher.worldRotation.add(this.addRotaionSpeed));
        }
    }

    /**
     * 播放技能特效
     * @param type 
     */
    private playEffectAtPlayer(type: number) {
        const cfg = GameConfig.GemSkill.getElement(type + 1);
        if (cfg) {
            const effect = Extension.GameObjPool.getInstance().spawn(cfg.effect) as Gameplay.Particle;
            const char = Gameplay.getCurrentPlayer().character;
            effect.worldLocation = char.worldLocation;
            setTimeout(() => {
                effect.play();
                // char.detac (effect);
                let worldScale = 1;
                const inter = setInterval(() => {
                    worldScale += 0.2;
                    if (worldScale >= 5) {// 扩大10倍
                        clearInterval(inter);
                        effect.setWorldScale(Type.Vector.one);// = new Type.Vector(1);
                        effect.stop();
                        Extension.GameObjPool.getInstance().despawn(effect);
                        return;
                    }
                    effect.setWorldScale(new Type.Vector(worldScale));// = ;
                }, 50);
            }, 100);

        } else {
            throw new Error("skill type error,not find cfg");
        }
    }

}
